IG$.initDaD = function(el, panel) {
	var owner = panel,
		me = this,
		grid = panel;
	
	me.panel = panel;
	me.el = el;
	
	me.dropZone = $s.create($s.dropzone, el, {
		ddGroup: '_I$RD_G_',
		
		nodeouttimer: -1,
		
		getTargetFromEvent: function(e) {
			var px = e.browserEvent.pageX,
				py = e.browserEvent.pageY,
				renderer = grid.getRendererByPoint(px, py);
			
			if (renderer == null)
			{
				renderer = el;
			}
			
			return renderer;
		},
		
		notifyOut : function(dd, e, data){
			var me = this;
			if(me.lastOverNode){
				me.onNodeOut(me.lastOverNode, dd, e, data);
				me.lastOverNode = null;
			}
			
			grid.isDragging = false;
			grid.hideDropFeedback(e);
			
			if (me.accept == true)
			{
				if (grid.sheetobj)
				{
					if (me.nodeouttimer > -1)
					{
						clearTimeout(me.nodeouttimer);
					}
					
					me.nodeouttimer = setTimeout(function() {
						grid.hideDropFeedback(e);
					}, 100);
					// grid.sheetobj.procUpdateReport();
				}
			}
		},
		
		onNodeEnter : function(target, dd, e, data){
			grid.isDragging = true;
			
			var dt, dttype,
				accept = false;
				
			if (data.records && data.records.length > 0 && data.records[0].data.type)
			{
				dt = data.records[0].data;
				dttype = dt.type.toLowerCase();
				
				accept = true;
			}
			
			if (accept == true)
			{
				grid.showDropHelper(dt);
			}
			
			this.accept = accept;
		},
		onNodeOut : function(target, dd, e, data){
			
		},
		onNodeOver : function(target, dd, e, data){
			var me = this,
				dt,
				ret = (me.accept == true);
			if (me.accept == true)
			{
				if (data.records && data.records.length > 0)
				{
					dt = data.records[0].data;
				}
				else
				{
					dt = data.cellData;
				}
				
				grid.showDropFeedback(e, dt);
			}
			return ret;
		},
		onNodeDrop : function(target, dd, e, data){
			var me = this,
				dt;

			grid.hideDropFeedback(e);
			
			if (me.accept == true)
			{
				if (data.records && data.records.length > 0)
				{
					dt = data.records[0].data;
				}
				else
				{
					dt = data.cellData;
				}
				
				grid.onDropWidget(e, dt);
			}
			
			me.accept = false;
			return true;
		}
	});
}