	{purpose: "export", desc: IRm$.r1('L_P_export'), group: "reporting"},
	
	{purpose: "open_win", desc: IRm$.r1('L_P_open_win'), group: "application"},
	{purpose: "open_dlg", desc: IRm$.r1('L_P_open_dlg'), group: "application"},
	{purpose: "close", desc: IRm$.r1('L_P_close'), group: "application"},
	
	{purpose: "execute_script", desc: IRm$.r1('L_P_execute_script'), group: "program"},
	{purpose: "servercall", desc: IRm$.r1('L_P_servercall'), group: "program"},
	{purpose: "restcall", desc: IRm$.r1('L_P_restcall'), group: "program"},
	{purpose: "mapping", desc: IRm$.r1('L_P_mapping'), group: "program"},

	{purpose: "thread_start", desc: IRm$.r1("L_P_thread_start"), group: "condition"},
	{purpose: "thread_end", desc: IRm$.r1("L_P_thread_end"), group: "condition"},
	{purpose: "cond_if", desc: IRm$.r1("L_P_cond_if"), group: "condition"},
	{purpose: "cond_else", desc: IRm$.r1("L_P_cond_else"), group: "condition"},
	{purpose: "cond_end", desc: IRm$.r1("L_P_cond_end"), group: "condition"},

	{purpose: "bpmn_confirm", desc: IRm$.r1("L_P_bpmn_confirm"), group: "bpmn"},
	{purpose: "bpmn_resolve", desc: IRm$.r1("L_P_bpmn_resolve"), group: "bpmn"}
