	_toggle: function(view) {
		var me = this,
			m_mview = me.down("[name=m_mview]"),
			l = m_mview.getLayout(),
			sid,
			r,
			t_c1 = me.down("[name=t_c1]"),
			t_c2 = me.down("[name=t_c2]"),
			lname = l.getActiveItem().name,
			l_1a = me.down("[name=l_1]").getLayout().getActiveItem(),
			vseq = parseInt(view.substring(1)),
			ga;
			
		if ((view == "m1" && lname != "l_a") || (view == "m2" && lname != "l_2") || (view == "m3" && lname != "l_3"))
		{
			if (vseq == 1 || vseq == 2)
			{
				if (lname == "l_3")
				{
					r = l.getActiveItem().commitCode();
				}
				else
				{
					r = l_1a.commitCode ? l_1a.commitCode() : true;
				}
			}
			else
			{
				ga = l.getActiveItem().getLayout().getActiveItem();
				if (ga && ga.commitCode)
				{
					r = ga.commitCode();
				}
				else
				{
					r = 1;
				}
			}
			
			if (r)
			{
				$.each(["m1", "m2", "m3"], function(i, k) {
					var btn = me.down("[name=t_" + k + "]");
					btn.toggle(k == view);
				});
				l.setActiveItem(vseq - 1);
				if (vseq == 1)
				{
					m_mview.sl = "l_a";
					sid = l_1a.sm;
				}
				else if (vseq == 2)
				{
					m_mview.sl = "l_2";
					sid = me.down("[name=" + m_mview.sl + "]").getLayout().getActiveItem().sm;
				}
				else if (vseq == 3);
				{
					m_mview.sl = "l_3";
				}
				t_c1.setVisible(vseq == 3 ? false : true);
				t_c2.setVisible(vseq == 3 ? false : true);
				t_c1.toggle(sid == 0 ? true : false);
				t_c2.toggle(sid == 1 ? true : false);
				
				vseq == 1 && me.refreshCode();
				vseq == 2 && l.getActiveItem().getLayout().getActiveItem().refreshCode();
				vseq == 3 && l.getActiveItem().refreshCode();
			}
		}
	},

	_toggle_c: function(view) {
		var me = this,
			m_mview = me.down("[name=m_mview]"),
			l = m_mview.getLayout(),
			aview = l.getActiveItem(),
			s_view_l,
			r, sm,
			vseq = parseInt(view.substring(1)),
			s_view_a;
		
		if (aview.name == "l_a")
		{
			s_view_l = aview.down("[name=l_1]").getLayout();
		}
		else
		{
			s_view_l = aview.getLayout();
		}
		
		sm = s_view_l.getActiveItem().sm;

		if ((view == "c1" && sm != 0) || (view == "c2" && sm != 1))
		{
			s_view_a = s_view_l.getActiveItem();
			r = s_view_a.commitCode ? s_view_a.commitCode() : (aview.name == "l_a" && view == "c2");
			
			if (r)
			{
				$.each(["c1", "c2"], function(i, k) {
					var btn = me.down("[name=t_" + k + "]");
					btn.toggle(k == view);
				});
				
				s_view_l.setActiveItem(vseq - 1);
				s_view_l.getActiveItem().refreshCode && s_view_l.getActiveItem().refreshCode();
			}
		}
	},

	de_upd_cube: function() {
		var me = this,
			framecontent = me.framecontent,
			cube_obj;
			
		if (framecontent && framecontent.cube_objects && framecontent.cube_objects.length)
		{
			cube_obj = framecontent.cube_objects[0];
			me.down("[name=tbcubepath]").setText(cube_obj.name);
		}
		else
		{
			me.down("[name=tbcubepath]").setText("");
		}
	},
	
